  const { account } = useWallet()

  

  const accountEllipsis = account ? `${account.substring(0, 4)}...${account.substring(account.length - 4)}` : null

  const [NFTs, setNFTs] = useState([])
  const [currentTab, setCurrentTab] = useState('owned');

  const filterNFTs = useCallback(async (rarity = '') => {
    try {
      const newNftContract = getNewNftContract()

      const nftData = []
    
      nfts.forEach(async (nft) => {
        let tradeIds = [];
        if(account) {
          tradeIds = await newNftContract.methods.getTradesByNftIdAndUser(account, nft.nftId).call()
        }
        if (isArray(tradeIds) && tradeIds.length > 0) {
          tradeIds.forEach((tradeId) => {
            nftData.push(getNftData(parseInt(tradeId, 10), nft.nftId))
          })
        }
      })
      setNFTs(nftData);
    } catch (error) {
      console.log("error");
      console.error(error);
    }
  }, [account])

  const filterListedNFTs = useCallback(async () => {
    try {
      const newNftContract = getNewNftContract()

      const nftData = []
    
      nfts.forEach(async (nft) => {
        let tradeIds = [];
        let listOpenTradeIds = [];
        let commonTradeIds = []
        if(account) {
          tradeIds = await newNftContract.methods.getTradesByNftIdAndUser(account, nft.nftId).call()
          listOpenTradeIds = await newNftContract.methods.getListOpenTradesByNftId(nft.nftId).call()
          commonTradeIds = tradeIds.filter(value => listOpenTradeIds.includes(value));
        }
        if (isArray(commonTradeIds) && commonTradeIds.length > 0) {
          commonTradeIds.forEach((tradeId) => {
            nftData.push(getNftData(parseInt(tradeId, 10), nft.nftId))
          })
        }
      })
      setNFTs(nftData);
    } catch (error) {
      console.log("error");
      console.error(error);
    }
  }, [account])

  const handleTab = (tab) => {
    setCurrentTab(tab);
  }


  const getNftData = (tradeId: number, nft: number) => {
    try {
      const nftDetailLink = `/new-detail/${nft}`
      const nftPreviewImage = nfts.filter((data) => nft === data.nftId).map((data) => data.previewImage)
      const nftName = nfts.filter((data) => nft === data.nftId).map((data) => data.name)
      const description = nfts.filter((data) => nft === data.nftId).map((data) => data.description)

      console.log("nftName", nftName)
      return {
        tradeId,
        name: nftName[0] || '',
        description: description[0] || '',
        previewImage: nftPreviewImage[0] || '',
        nftDetailLink,
        nftId: nft,
      }
    } catch (error) {
      return null
    }
  }



